<template>
<section class="center-newsPapers">
  <div class="container mx-auto p-2 sm:mx-1 max-w-screen-md">
    <div class="w-full h-auto">
      <p class="font-bold text-black text-start text-sm">گیشه روزنامه</p>
      <div class="w-full bg-slate-600 h-0.5 my-1"></div>
      <div class="flex flex-row align-center justify-around w-full">
        <div :class="{hide : counter === 5}" class="w-1/12 mx-1">
          <div class="w-12 h-12 rounded-full bg-slate-300 relative">
          <font-awesome-icon @click="increment" :icon="['fas','angle-left']" class="absolute text-md mx-auto cursor-pointer right-4 top-4 font-bold text-white"></font-awesome-icon>
          </div>
        </div>
        <Transition :duration="500" name="fade" mode="out-in">
        <div class="w-10/12">
          <div v-for="items in list2" :key="items">
              <img @erro="notShow" style="height: 300px" :src="`${items.newspapers[counter].url}`" alt="" class="w-full rounded-sm">
          </div>
        </div>
        </Transition>
        <div :class="{hide : counter === 0}" class="w-1/12 mx-1">
          <div class="w-12 h-12 rounded-full bg-slate-300 relative">
            <font-awesome-icon @click="decrement" :icon="['fas','angle-right']" class="absolute text-md mx-auto cursor-pointer right-4 top-4 font-bold text-white"></font-awesome-icon>
          </div>
        </div>
      </div>

    </div>
  </div>
</section>
</template>

<script>
import {ref, reactive, onMounted} from 'vue'
import axios from 'axios'
import {FontAwesomeIcon} from "@fortawesome/vue-fontawesome";
import {handleError} from "@/components/composable/handleError";
export default {
  name: "NewsPapers",
  components:{FontAwesomeIcon},
  setup(){
    let counter=ref(0)
    const showSlider=ref(false)
    const sizeTitle=ref(12)
    const size=ref(12)
    const list2=reactive([])
    const total=onMounted(async ()=>{
      await axios.get('https://api.footballi.net/api/v2/news/newspapers')
      .then((response)=>{
        if(response.data.success){
          list2.push(response.data.data[0])
        }
      })
      .catch((error)=>{
        handleError(error)
      })

    })
    function increment(){
     counter.value++
    }
    function decrement(){
      counter.value--;
    }
    const notFound=ref('https://footballi.net/assets/shared-application/images/placeholders/ic_default_news_image.png')
    function notShow(event){
      event.target.src=notFound.value
    }
    return{
      showSlider,
      size,
      sizeTitle,
      list2,
      total,
      counter,
      notShow,
      notFound,
      increment,
      decrement,
    }
  }
}
</script>

<style scoped>
.hide{
 visibility: hidden;
}
.fade-enter-active,
.fade-leave-active{
  transition: all ease-in-out 0.5s;
}
.fade-enter-from,
.fade-leave-to{
  opacity: 0;
}
</style>